<template>
    <div>
        <center><img :src="this.main"></center>
        <h2>{{ message }}</h2>
        <h3 v-show="this.isActive">{{ now }}.</h3>
        <div v-show="this.isActive">
        <ol>
            <li @click="ck_today">오늘의 운세</li>
            <li @click="ck_career">직장운</li>
            <li @click="ck_love">연애운</li>
            <li @click="ck_money">금전운</li>
        </ol>
        </div>
        <h2 id='result' v-show="this.isRetry">{{ result }}</h2>
      <h3 id="retry" @click="retry" v-show="this.isRetry">다시하기</h3>
    </div>
</template>

<script>

export default {
 
  data() {
    return {
      //Display
      isActive: true,
      isRetry: false,
      tryCount: 0,

      //clock
      now: new Date(),

      //message
      message: "머신소울이 알려드립니다.",

      //Image
      mainImg:"data:image/png;base64,",
      resultImg:"data:image/png;base64,",
      processingImg:'/img/computer.1bd2300b.gif',
      main: "",

      //fortuneTelling
      result:"",
      anwser_today:["아주 좋습니다.","노력해야 되겠어요.","자중하세요.","주위 사람과 시간을 보내세요.","문서에 유의하세요.","즐거운 시간이 기다리고 있어요."],
      anwser_career:["자신감을 가지고 임하세요.","노력해야 되겠어요.","자중하세요.","준비가 필요합니다.","될 가능성이 높아요.","능력치 향상이 필요합니다."],
      anwser_love:["마음껏 사랑하세요.","노력해야 되겠어요.","자중하세요.","가능성이 낮습니다.","동상이몽이네요.","당신을 생각하고 있네요."],
      anwser_money:["노력한 만큼 법니다.","주위에 나누세요.","자중하세요.","선물이 기다리고 있네요.","자린고비정신이 필요합니다.","작은 이익이 있어요."],
      result_num:1

    } 
  },
  methods: {  
      retry: function() {
        this.isActive = true;
        this.isRetry = false;
        if (this.tryCount > 2) {
          this.message = "더 궁금한게 남아있나요?";
        } else {
          this.message = "다시 뵙네요.";
        }
        this.main =this.mainImg;
      },

      count_time: function() {
        this.now = new Date();
      },

      load_intro: function() {
        this.isActive = false;
        this.message = "";
        this.main = this.processingImg;
        this.message = "계산중...";
      },
      load_result: function(menu) { 
        this.main = this.resultImg;
        this.message = "계산 결과가 나왔습니다.";
        this.calc_resultNum(menu);
        this.isRetry = true;
        this.tryCount ++;
      }, 
      calc_resultNum: function(menu) {
        this.result_num = Math.floor(Math.random() *5);
        this.result = menu[this.result_num];
      },

      ck_today: function() {
        this.load_intro();
        setTimeout(this.load_result,2500,this.anwser_today);
      },
      ck_career: function() { 
        this.load_intro();
        setTimeout(this.load_result,2500,this.anwser_career);
      },
      ck_love: function() {
        this.load_intro();
        setTimeout(this.load_result,2500,this.anwser_love);
      },
      ck_money: function() {
        this.load_intro();
        setTimeout(this.load_result,2500,this.anwser_money);
      }
  },
  created() {
    this.main = this.mainImg;
    setInterval(this.count_time,1000);
  },
}
</script>
<style>
@font-face {
  font-family: 'dunggunmo' ;
  src: url('../assets/DungGeunMo.ttf');
}
h2,li,ol,h3 {
  font-family: 'dunggunmo';
  text-align: center;
}
li {
  list-style-type: none;
  margin-right: 5%;
  padding:5px;
}
li:hover {
  color: grey;
}
#result {
  color:blue;
}
#retry:hover {
  color: grey;
}
</style>